
#uicore-support-agent{position:fixed;right:30px;bottom:30px;z-index:9999;background:#fff;border:1px solid #e5e7eb;box-shadow:0 4px 4px hsla(0,0%,68.2%,.25),0 9px 40px -15px hsla(0,0%,68.2%,.25);border-radius:6px;width:440px;height:95vh;max-height:610px;color:#0f172a;display:flex;flex-direction:column
}
#uicore-support-agent .uisupport-header{border-bottom:1px solid #f1f5f9;padding:4px 10px;line-height:1.9;font-size:14px;display:flex;align-items:center;justify-content:space-between
}
#uicore-support-agent .uisupport-header .uisupport-title{font-weight:500
}
#uicore-support-agent .uisupport-header .uisupport-title span{background:#532df5;border-radius:4.5px;color:#fff;font-size:10px;line-height:13px;font-weight:600;padding:4px 6px 4px 7px;letter-spacing:.4px;margin-left:5px
}
#uicore-support-agent .uisupport-header svg{max-width:16px;max-height:15px;vertical-align:text-top;opacity:.6;cursor:pointer
}
#uicore-support-agent .uisupport-header svg:hover{opacity:1
}
#uicore-support-agent .uisupport-content{height:100%
}
.uisupport-start{position:fixed;top:25vh;right:0;background:#fff;border:1px solid #e5e7eb;border-radius:6px 0 0 6px;padding:10px;z-index:9999;box-shadow:0 4px 4px hsla(0,0%,68.2%,.25),0 9px 40px -15px hsla(0,0%,68.2%,.25)
}
.uisupport-start:hover{cursor:pointer
}
.uisupport-start:hover svg path:not(:first-child){animation:uisupporticon 1.2s linear infinite;animation-delay:-.9s
}
.uisupport-start:hover svg path:nth-child(3){animation-delay:-.7s
}
.uisupport-start:hover svg path:nth-child(4){animation-delay:-.6s
}
@keyframes uisupporticon{
0%,60%,to{transform:none
}
30%{transform:translateY(-3px)
}
}
.uisupport-content{display:flex;flex-direction:column;overflow:hidden
}
.uisupport-content .uisupport-chat__content{flex:1;overflow-y:auto;padding:10px
}
.uisupport-content .uisupport-chat__content__message{display:flex;align-items:flex-end;margin-bottom:10px
}
.uisupport-content .uisupport-chat__content__message .uisupport-chat__content__message__content{background:#f1f5f9;border-radius:14px;padding:10px;max-width:80%
}
.uisupport-content .uisupport-chat__content__message .uisupport-chat__content__message__content .uisupport-chat__content__message__content__text{font-size:14px;white-space:pre-wrap;word-break:normal;word-spacing:normal;word-wrap:normal;-webkit-hyphens:none;hyphens:none
}
.uisupport-content .uisupport-chat__content__message--right{justify-content:flex-end
}
.uisupport-content .uisupport-chat__content__message--right .uisupport-chat__content__message__content{background:#532df5
}
.uisupport-content .uisupport-chat__content__message--right .uisupport-chat__content__message__content .uisupport-chat__content__message__content__text{color:#fff
}
.uisupport-content .uisupport-chat__content__message .uisupport-dot{display:inline-block;width:4px;height:4px;border-radius:50%;margin:0 4px;background:#532df5;animation:uisupportwave 1.2s linear infinite;animation-delay:-.9s
}
.uisupport-content .uisupport-chat__content__message .uisupport-dot.uisupport-dot_two{animation-delay:-.7s;margin:0 4px 0 0
}
.uisupport-content .uisupport-chat__content__message .uisupport-dot.uisupport-dot_three{margin:0 4px 0 0;animation-delay:-.6s
}
.uisupport-content .uisupport-chat__tip{text-align:center;padding:30px;display:flex;flex-direction:column;justify-content:end
}
.uisupport-content .uisupport-chat__footer{display:flex;align-items:center;padding:10px;position:relative
}
.uisupport-content .uisupport-chat__footer .uisupport-chat__footer__input{flex:1
}
.uisupport-content .uisupport-chat__footer .uisupport-chat__footer__input input{width:100%;border:1px solid #e5e7eb;border-radius:10px;padding:10px;font-size:14px
}
.uisupport-content .uisupport-chat__footer .uisupport-chat__footer__input input:focus{outline:none
}
.uisupport-content .uisupport-chat__footer .uisupport-chat_error{position:absolute;top:25px;left:0;right:0;text-align:center;color:#f44336;font-size:12px;padding:5px 0
}
@keyframes uisupportwave{
0%,60%,to{transform:none
}
30%{transform:translateY(-6px)
}
}